/*
 * UCLA Lists styles
 */
ul {
  list-style: none;
  line-height: 24px;
}

ul li {
  margin-bottom: 16px;
}

ul li::before {
  content: "\25AA";
  font-weight: bold;
  color: #2774AE;
  display: inline-block;
  width: 1em;
  margin-left: -18px;
}

li ul li::before {
  content: "\25A1";
}

ul li ol, ul li ul {
  margin-top: 16px;
}

ol {
  list-style: none;
  counter-reset: li;
  line-height: 24px;
}

ol li {
  margin-bottom: 16px;
  counter-increment: li;
}

ol li::before {
  content: counter(li) ".";
  color: #2774AE;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
  margin-right: 1.3em;
}

ol li ol, ol li ul {
  margin-top: 16px;
}

.stat-wrapper {
  /* Use the .stat-tout class for VERTICAL stat touts

	Example HTML structure (from Facts & Figures):

	<div class="stat-wrapper clearfix">
		<div class="stat-tout">
			<span class="stat-tout__number">31,000</span>
			<span class="stat-tout__label">Undergraduate Students</span>
		</div>
	</div>

	*/
}

.stat-wrapper .stat-tout {
  display: block;
  border-left: 4px solid #FFD100;
  padding: 6px 0 38px 32px;
  margin: 24px 24px 0;
}

@media (min-width: 768px) {
  .stat-wrapper .stat-tout {
    display: inline-block;
  }
}

.stat-wrapper .stat-tout__number {
  font-size: 54px;
  line-height: 4rem;
  color: #333;
  font-weight: bold;
  display: block;
  margin-bottom: 8px;
}

.stat-wrapper .stat-tout__label {
  font-size: 24px;
  line-height: 24px;
  font-weight: bold;
  display: block;
  max-width: 212px;
}

@media (min-width: 768px) {
  .stat-wrapper .stat-tout__label {
    max-width: 312px;
  }
}

.stat-wrapper .stat-tout__label--grey {
  color: #666666;
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: bold;
  display: block;
}

.stat-wrapper .stat-tout--wide {
  width: 100%;
  border-bottom: 4px solid #FFD100;
  padding-bottom: 24px;
  margin-bottom: 48px;
}

@media only screen and (min-width: 768px) {
  .stat-wrapper .stat-tout {
    width: calc((100%/2) - (24px/2));
    float: left;
    margin-right: 24px;
  }
  .stat-wrapper .stat-tout:nth-child(2n) {
    margin-right: 0;
  }
}

@media only screen and (min-width: 1024px) {
  .stat-wrapper .stat-tout {
    width: calc((100%/3) - (48px/3));
    float: left;
    margin-right: 24px;
  }
  .stat-wrapper .stat-tout:nth-child(2n) {
    margin-right: 24px;
  }
  .stat-wrapper .stat-tout:nth-child(3n) {
    margin-right: 0;
  }
}

.fixed-table {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.fixed-table__scroller {
  margin-left: calc(36% + 16px);
  /* If you change this line you must also change the nested class ".fixed-table__sticky-col" width to match. */
  overflow-x: auto;
  overflow-y: visible;
}

@media (min-width: 768px) {
  .fixed-table__scroller {
    overflow-x: visible;
    margin-left: 0;
  }
}

.fixed-table__wrapper {
  border: none;
  border-spacing: 0;
