}

.fixed-table #sortTable {
  opacity: 0;
  filter: alpha(opacity=0);
}

.fixed-table #sortTable tr th:nth-child(1) {
  position: absolute;
}

@media (min-width: 768px) {
  .fixed-table #sortTable tr th:nth-child(1) {
    position: relative;
  }
}

.fixed-table #sortTable tr th:nth-child(1) {
  width: 34%;
}

.fixed-table #sortTable th {
  position: relative;
  cursor: pointer;
  padding-left: 32px;
}

.fixed-table #sortTable th::before, .fixed-table #sortTable th::after {
  content: '\25E4';
  font-size: 6px;
  color: #333333;
  display: block;
  position: absolute;
  left: 18px;
  top: 12px;
  width: 6px;
  height: 9px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.fixed-table #sortTable th::after {
  left: 16px;
  top: 16px;
  -webkit-transform: rotate(225deg);
  -moz-transform: rotate(225deg);
  -ms-transform: rotate(225deg);
  -o-transform: rotate(225deg);
  transform: rotate(225deg);
}

.fixed-table #sortTable th.sorting-asc::after {
  color: #00598C;
}

.fixed-table #sortTable th.sorting-desc::before {
  color: #00598C;
}

.fixed-table__sticky-col {
  left: 0;
  position: absolute;
  top: auto;
  width: 36%;
  /* If you change this line change you must also change the parent class ".fixed-table__scroller" margin-left to match. */
  overflow-y: scroll;
  font-weight: bold;
}

@media (min-width: 768px) {
  .fixed-table__sticky-col {
    min-width: 300px;
    position: relative;
  }
}

.fixed-table ::-webkit-scrollbar {
  width: 0;
  height: 8px;
}

.fixed-table ::-webkit-scrollbar-track {
  box-shadow: inset 0 -1px 0 0 #E5E5E5, inset 0 1px 0 0 #E5E5E5;
  border-radius: 4px;
}

.fixed-table ::-webkit-scrollbar-thumb {
  background: #00598C;
  border-radius: 4px;
}

.fixed-table ::-webkit-scrollbar-thumb:hover {
  background: #0079BF;
}

.text, .radio, .select, .checkbox {
  margin-bottom: 8px;
}

.text__label, .text__input, .text__help, .text__error {
  display: block;
}

.text__label {
  display: block;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
  margin-top: 8px;
  margin-bottom: 8px;
}

.text__input {
  box-sizing: border-box;
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
  background: #EBEBEB;
  background-size: 24px;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 24px 8px 16px;
  border: 1px solid #808080;
  border-radius: 2px;
  color: #666666;
}

.text__input:focus {
  outline: 1px dotted #0079BF;
  outline-offset: 1px;
}

.text.full-width > .text__input {
  width: 100%;
  max-width: 100%;
}

.text__help {
  display: block;
  color: #666666;
  margin-top: 8px;
}

.text__error {
  display: none;
}

.text.hasError > .text__input {
  border: 1px solid red;
}

.text.hasError > .text__error {
  display: block;
  color: #D60000;
  margin-top: 8px;
}

.select__label {
  display: block;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
  margin-top: 8px;
  margin-bottom: 8px;
}

.select.full-width > .select__menu {
  width: 100%;
}

.select__menu {
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
  background: #EBEBEB url("/img/icons/arrow-down-defaultblue.svg") no-repeat 98.5% 50%;
  background-size: 24px;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 24px 8px 16px;
  border: 1px solid #808080;
  border-radius: 2px;
  color: #666666;
}

.select__menu::-ms-expand {
  display: none;
  /* Hide the default arrow in Internet Explorer 10 and Internet Explorer 11 */
}

.select__menu:focus {
  outline: 1px dotted #0079BF;
  outline-offset: 1px;
}

.select__menu:active {
  color: #000000;
}

.select__help {
  display: block;
  color: #666666;
  margin-top: 8px;
}

.select__error {
  display: none;
}

.select.hasError > .select__menu {
  border: 1px solid red;
  background: #EBEBEB url("/img/icons/error-red.svg") no-repeat 96.5% 50%;
  background-size: 24px 24px;
}

.select.hasError > .select__error {
  display: block;
  color: #D60000;
  margin-top: 8px;
}

.checkbox__input {
  appearance: none;
  -webkit-appearance: none;
  border: 1px solid #cacece;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 9px;
  border-radius: 2px;
  display: inline-block;
  margin-right: 12px;
}

.checkbox__input:hover {
  background: #0079BF;
}

.checkbox__input:focus {
  outline-style: dotted;
  outline-offset: 1px;
}

.checkbox__input:active, .checkbox__input:checked:active {
  outline-offset: -1px;
}

.checkbox__input:checked {
  background-color: #003B5C;
  border: 1px solid #adb8c0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05), inset 15px 10px -12px rgba(255, 255, 255, 0.1);
  padding: 0 4px;
}

.checkbox__input:checked::after {
  background-image: url("/img/icons/checkmark-white.svg");
  background-size: 10px 10px;
  display: inline-block;
  width: 10px;
  height: 10px;
  content: "";
}

.checkbox__input:checked {
  background-color: #003B5C;
  border: 1px solid #adb8c0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05), inset 15px 10px -12px rgba(255, 255, 255, 0.1);
  padding: 0 4px;
}

.radio__input {
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
  display: inline-block;
  height: 24px;
  width: 24px;
  border-radius: 12px;
  border: 1px solid #808080;
  background-clip: content-box;
  margin-right: 11px;
}

.radio__input:hover {
  background: #0079BF;
  border-color: #0079BF;
}

.radio__input:checked {
  background: #003B5C;
  border-color: #003B5C;
}

.radio__input:checked::after {
  background-image: url("/img/icons/radio-bullet-icon.svg");
  background-size: 25px 25px;
  background-repeat: none;
  background-position: 50%;
  display: inline-block;
  content: "";
  width: 22px;
  height: 22px;
}

.radio__input:focus {
  outline-style: dotted;
  outline-offset: 1px;
}

.fieldset {
